/* Button styles */
.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
}

/* Hover effect */
.button:hover {
    background-color: #45a049;
}

/* Active effect */
.button:active {
    background-color: #3e8e41;
}
