body {
  background-color: #f8f9fa;
  font-family: 'Roboto', sans-serif;
  display: block;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  margin-left:300px;
  margin-right:300px;
}

.container {
  max-width: 500px;
  width: 100%;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}

form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

input,
textarea {
  border: 2px solid #ccc;
  border-radius: 6px;
  outline: none;
  font-size: 16px;
  width: 80%;
  margin: 11px 0px;
  padding: 7px;
}

label {
  margin-bottom: 8px;
}

.btn {
  color: white;
  background: purple;
  padding: 8px 12px;
  font-size: 16px;
  border: 2px solid white;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  margin-top: 10px; 
}

.btn:hover {
  background: #4527a0;
}

table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

th,
td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

td img {
  display: block;
  margin: 0 auto;
  max-width: 50px; 
}

.btn-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.btn-container a {
  margin-right: 10px;
}

.view {
      color: white;
      background: red;
      padding: 8px 12px;
      font-size: 20px;
      border: 2px solid white;
      border-radius: 14px;
      cursor: pointer;
      align-items: center;
  }

  .viewbut {
